<script setup lang="ts">
import { ref, computed, watch } from 'vue'
import type { PropType } from 'vue'
import type { PageConfig } from '@/types'
import ImageAnnotation from '@/components/ImageAnnotation.vue'

const props = defineProps({
  // Note: config here might be a merged "Group Config" containing multiple pages
  // or a single page config. We need to handle the "grouped" logic.
  // The parent component (HomePage) should pass the current active page config,
  // but also the list of sibling pages in the same group so we can render tabs.
  config: {
    type: Object as PropType<PageConfig>,
    required: true
  },
  groupPages: {
    type: Array as PropType<PageConfig[]>,
    default: () => []
  }
})

const emit = defineEmits(['navigate-to-page'])

const activePageId = computed(() => props.config.id)

const handleTabClick = (pageId: string) => {
  emit('navigate-to-page', pageId)
}

const isSteps = computed(() => props.config.groupType === 'steps')
</script>

<template>
  <div class="h-full w-full flex items-center justify-center bg-slate-50 p-8">
    <div class="max-w-6xl w-full h-full flex flex-col">
      <div class="mb-6 shrink-0">
        <h2 class="text-3xl font-bold text-slate-900 mb-2">{{ config.title }}</h2>
        <p v-if="config.subTitle" class="text-slate-500">{{ config.subTitle }}</p>
      </div>

      <div class="flex-1 min-h-0 flex gap-6">
        <!-- Navigation Sidebar (Tabs or Steps) -->
        <div v-if="groupPages.length > 0" class="w-64 shrink-0 flex flex-col gap-2 overflow-y-auto pr-2">
          
          <!-- Tabs Style -->
          <template v-if="!isSteps">
            <button
              v-for="page in groupPages"
              :key="page.id"
              class="w-full text-left p-4 rounded-xl transition-all duration-200 border-2"
              :class="[
                activePageId === page.id 
                  ? 'bg-white border-blue-600 shadow-md transform scale-[1.02]' 
                  : 'bg-slate-100 border-transparent hover:bg-white hover:border-slate-200 text-slate-500'
              ]"
              @click="handleTabClick(page.id)"
            >
              <h3 class="font-bold mb-1" :class="activePageId === page.id ? 'text-blue-700' : 'text-slate-700'">
                {{ page.contentTitle }}
              </h3>
              <p class="text-xs" :class="activePageId === page.id ? 'text-blue-500' : 'text-slate-400'">
                {{ page.contentSubtitle }}
              </p>
            </button>
          </template>

          <!-- Steps Style -->
          <template v-else>
             <div class="relative pl-4 space-y-8">
               <!-- Vertical Line -->
               <div class="absolute left-[19px] top-4 bottom-4 w-0.5 bg-slate-200"></div>

               <button
                v-for="(page, index) in groupPages"
                :key="page.id"
                class="relative w-full text-left group flex items-start gap-4"
                @click="handleTabClick(page.id)"
              >
                <!-- Step Dot -->
                <div 
                  class="relative z-10 w-3 h-3 rounded-full mt-1.5 transition-all duration-300 shrink-0"
                  :class="[
                    activePageId === page.id 
                      ? 'bg-blue-600 ring-4 ring-blue-100 scale-125' 
                      : 'bg-slate-300 group-hover:bg-slate-400'
                  ]"
                ></div>

                <div class="transition-opacity duration-200" :class="activePageId === page.id ? 'opacity-100' : 'opacity-60 group-hover:opacity-80'">
                   <h3 class="font-bold text-sm" :class="activePageId === page.id ? 'text-slate-900' : 'text-slate-600'">
                    {{ page.contentTitle }}
                  </h3>
                  <p class="text-xs text-slate-400 mt-0.5">
                    {{ page.contentSubtitle }}
                  </p>
                </div>
              </button>
             </div>
          </template>
        </div>

        <!-- Main Content Area (Image Annotation) -->
        <div class="flex-1 bg-white rounded-2xl shadow-sm border border-slate-200 overflow-hidden relative">
          <div class="absolute inset-0 p-4">
             <ImageAnnotation 
                v-if="config.image"
                :src="config.image" 
                :annotations="config.annotations || []"
                :key="config.id"
              />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>