<script setup lang="ts">
import { ref, onMounted, onUnmounted, nextTick, watch } from 'vue'
import { Info, X, ExternalLink } from 'lucide-vue-next'
import type { Annotation } from '@/types'

const props = defineProps<{
  src: string
  alt?: string
  annotations: Annotation[]
}>()

const activeAnnotation = ref<Annotation | null>(null)

// Reset active annotation when props change (e.g. switching tabs/images)
watch(() => props.src, () => {
  activeAnnotation.value = null
})
const imageRef = ref<HTMLImageElement | null>(null)
const containerRef = ref<HTMLElement | null>(null)
const imageRect = ref({ width: 0, height: 0, left: 0, top: 0 })

const updateImageRect = () => {
  if (imageRef.value && containerRef.value) {
    const img = imageRef.value
    const container = containerRef.value
    
    // Calculate the actual displayed size of the image within object-contain
    const imgRatio = img.naturalWidth / img.naturalHeight
    const containerRatio = container.clientWidth / container.clientHeight
    
    let displayedWidth, displayedHeight, left, top

    if (containerRatio > imgRatio) {
      // Image is height-constrained
      displayedHeight = container.clientHeight
      displayedWidth = displayedHeight * imgRatio
      left = (container.clientWidth - displayedWidth) / 2
      top = 0
    } else {
      // Image is width-constrained
      displayedWidth = container.clientWidth
      displayedHeight = displayedWidth / imgRatio
      left = 0
      top = (container.clientHeight - displayedHeight) / 2
    }

    imageRect.value = {
      width: displayedWidth,
      height: displayedHeight,
      left,
      top
    }
  }
}

const handleMouseEnter = (annotation: Annotation) => {
  activeAnnotation.value = annotation
  // Start animation loop to sync rects during transition
  const startTime = performance.now()
  const duration = 350 // Slightly longer than CSS transition (300ms)
  
  const animate = (currentTime: number) => {
    updateImageRect()
    if (currentTime - startTime < duration) {
      requestAnimationFrame(animate)
    }
  }
  requestAnimationFrame(animate)
}

// Watch for window resize
onMounted(() => {
  window.addEventListener('resize', updateImageRect)
  // Initial calculation when image loads
  if (imageRef.value?.complete) {
    updateImageRect()
  } else {
    imageRef.value?.addEventListener('load', updateImageRect)
  }
  
  // Use ResizeObserver for more robust size tracking of the container
  const observer = new ResizeObserver(() => {
    updateImageRect()
  })
  if (containerRef.value) {
    observer.observe(containerRef.value)
  }
})

onUnmounted(() => {
  window.removeEventListener('resize', updateImageRect)
})

const closePopup = () => {
  activeAnnotation.value = null
  // Start animation loop to sync rects during transition
  const startTime = performance.now()
  const duration = 350 // Slightly longer than CSS transition (300ms)
  
  const animate = (currentTime: number) => {
    updateImageRect()
    if (currentTime - startTime < duration) {
      requestAnimationFrame(animate)
    }
  }
  requestAnimationFrame(animate)
}
</script>

<template>
  <div class="relative w-full h-full flex flex-col md:flex-row gap-4 transition-all duration-300">
    <!-- Image Container -->
    <div 
      ref="containerRef"
      class="relative bg-slate-100 rounded-xl overflow-hidden border border-slate-200 shadow-sm group h-full transition-all duration-300 ease-in-out"
      :class="activeAnnotation ? 'flex-[2]' : 'flex-1'"
    >
      <div class="relative w-full h-full">
        <img 
          ref="imageRef"
          :src="src" 
          :alt="alt || 'Annotation Image'"
          class="w-full h-full object-contain"
          @load="updateImageRect"
        />
        
        <!-- Hotspots Overlay Layer - Positioned absolutely over the ACTUAL image content -->
        <div 
          class="absolute pointer-events-none transition-all duration-300 ease-in-out"
          :style="{
            width: `${imageRect.width}px`,
            height: `${imageRect.height}px`,
            left: `${imageRect.left}px`,
            top: `${imageRect.top}px`
          }"
        >
          <!-- Actual Hotspots - Pointer events re-enabled -->
          <div 
            v-for="ann in annotations"
            :key="ann.headerTitle" 
            class="absolute border-2 border-dashed border-blue-400 bg-blue-500/10 hover:bg-blue-500/20 cursor-help transition-all duration-200 rounded-md pointer-events-auto"
            :class="{ 'border-solid border-green-500 bg-green-500/20': activeAnnotation?.headerTitle === ann.headerTitle }"
            :style="{
              left: `${ann.x}%`,
              top: `${ann.y}%`,
              width: `${ann.width}%`,
              height: `${ann.height}%`
            }"
            @mouseenter="handleMouseEnter(ann)"
          >
          </div>
        </div>
      </div>
    </div>

    <!-- Description Sidebar (Popup) -->
    <!-- Use relative flex sizing to push the image -->
    <div 
      class="relative shrink-0 flex flex-col justify-center transition-all duration-300 ease-in-out"
      :class="activeAnnotation ? 'w-80 opacity-100 translate-x-0' : 'w-0 opacity-0 translate-x-4'"
    >
       <div 
          v-if="activeAnnotation"
          class="w-80 h-auto max-h-full bg-white rounded-xl shadow-xl border border-slate-100 overflow-hidden flex flex-col pointer-events-auto z-10"
        >
          <div class="p-4 bg-slate-50 border-b border-slate-100 flex justify-between items-start shrink-0">
            <h3 class="font-bold text-lg text-slate-800">{{ activeAnnotation.headerTitle }}</h3>
            <button @click="closePopup" class="text-slate-400 hover:text-slate-600">
              <X class="w-5 h-5" />
            </button>
          </div>
          
          <div class="p-5 space-y-6 overflow-y-auto flex-1">
            <!-- Concept -->
            <div>
              <h4 class="text-xs font-bold text-slate-400 uppercase tracking-wider mb-2">{{ activeAnnotation.concept.title }}</h4>
              <p class="text-sm text-slate-600 leading-relaxed">
                {{ activeAnnotation.concept.description }}
              </p>
            </div>

            <!-- Keywords -->
            <div v-if="activeAnnotation.keywords && activeAnnotation.keywords.length">
              <h4 class="text-xs font-bold text-slate-400 uppercase tracking-wider mb-2">Keywords</h4>
              <div class="flex flex-wrap gap-2">
                <span 
                  v-for="kw in activeAnnotation.keywords" 
                  :key="kw"
                  class="px-2 py-1 bg-slate-100 text-slate-600 text-xs rounded-md font-medium"
                >
                  {{ kw }}
                </span>
              </div>
            </div>

            <!-- Link -->
            <div v-if="activeAnnotation.link">
              <h4 class="text-xs font-bold text-slate-400 uppercase tracking-wider mb-2">Resources</h4>
              <a 
                :href="activeAnnotation.link.url" 
                target="_blank"
                class="text-sm text-blue-600 hover:underline flex items-center gap-1"
              >
                {{ activeAnnotation.link.text }}
                <ExternalLink class="w-3 h-3" />
              </a>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>
